@import '../common/styles/sass/variables';

.header {
  height: 80px;
  background-color: $bgColor;

  .fixed {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 80px;
    background-color: $bgColor;
    z-index: 10;
  }
}
