@import "src/common/styles/sass/mixins";
@import "src/common/styles/sass/variables";

.mainBlock {
  min-height: 100vh;
  @include flex(row, space-around, center, no-wrap);
  background-image: url('../common/Images/backrgroun-section/543.jpg');
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;

  .text {
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    color: $textColor;
    font-family: 'Martel', serif;

    span {
      font-size: 16px;

      &:nth-child(2) {
        @include text(50px, 1.2);
        letter-spacing: 5px;
        margin: 10px 0;

        span {
          @include text(50px, 1.2);
          letter-spacing: 5px;
          position: relative;
          z-index: 0;
          display: inline-block;

          &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 20px;
            background-color: $primaryColor;
            bottom: 0;
            z-index: -1;
          }
        }
      }

      &:nth-child(3) {
        @include text(60px, 1.2);
      }
    }
  }

  .photo {
    position: relative;
    z-index: 1;
    @media screen and (max-width: 650px) {
      display: flex;
      justify-content: center;
    }

    .image {
      width: 300px;
      height: 400px;
      background-image: url('../assets/image/photo.jpg');
      background-repeat: no-repeat;
      background-position: 50% 0;
      background-size: cover;

      &:before {
        content: '';
        position: absolute;
        height: 110%;
        width: 105%;
        border: 5px solid $primaryColor;
        left: 15px;
        top: -15px;
        z-index: -1;
        @media screen and (max-width: 1178px) {
          left: -23px;
        }
        @media screen and (max-width: 650px) {
          width: 76%;
          left: 54px;
        }
      }
    }
  }
  @media screen and (max-width: 650px) {
    display: block;
  }

}


