.btn{border:none;text-transform:uppercase;letter-spacing:1px;padding:8px 20px;max-width:160px;width:100%;color:#fff;outline:none;position:relative;z-index:0}.btn::after{content:"";position:absolute;border-radius:5px;width:50%;height:10px;background:#c3313b;bottom:0;z-index:-1;transition:all .2s ease-in-out;left:50%;transform:translateX(-50%)}.btn:hover{text-decoration:none;color:#fff}.btn:hover span::after{content:"»"}.btn:hover::after{width:100%;height:100%;transition:all .3s ease-in-out 0s}